@use '../../styles/shared/transition';

:host {
    display: flex;
    height: 100%;
    width: 100%;
    align-items: stretch;
    overflow: hidden;
    position: relative;

    .backdrop {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.2);
        z-index: calc(var(--token-z-index-nav) - 1);
        cursor: pointer;
        will-change: opacity;
        transition: opacity var(--token-transition-time);
        animation: fadeIn 300ms forwards;
    }

    .nav {
        position: relative;
        width: var(--token-width-sidebar);
        display: flex;
        flex: 0 0 auto;
        min-height: 100%;
        background-color: var(--token-sidebar-background);
        color: var(--token-sidebar-foreground);
        overflow-y: auto;
        z-index: var(--token-z-index-nav);
        will-change: width;
        transition: width var(--token-transition-time);

        at-sidebar-trigger {
            display: flex;
            width: 100%;
        }
    }

    .nav-content {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        width: 100%;
        padding: 8px;
    }

    .content {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    i[slot='menu-item-actions'],
    * > [data-name='sidebar-menu-item-label'] {
        transition: opacity 150ms ease;
    }

    :not(at-menu) i[slot='menu-item-actions'],
    :not(at-menu) [data-name='sidebar-menu-item-label'] {
        opacity: 1;
    }

    at-sidebar-trigger {
        display: flex;
        justify-content: flex-end;
    }

    at-sidebar-menu {
        at-sidebar-submenu [slot='submenu-content'] {
            at-sidebar-menuitem {
                padding-left: 36px;
            }

            at-sidebar-submenu [slot='submenu-content'] {
                at-sidebar-menuitem {
                    padding-left: 50px;
                }

                at-sidebar-submenu [slot='submenu-content'] {
                    at-sidebar-menuitem {
                        padding-left: 64px;
                    }
                }
            }
        }
    }

    at-sidebar-submenu {
        at-menu {
            [data-name='menu-content-wrapper'] {
                background-color: var(--token-sidebar-background);
            }
        }
    }

    at-sidebar-trigger at-button [data-name='button-icon'] {
        font-size: var(--token-font-size-icon-xl) !important;
    }

    &.mode-over .nav {
        position: absolute;
    }

    &.collapse-icon {
        &.mode-over:not(.side-right) .content {
            padding-left: var(--token-width-sidebar-collapsed);
        }
        &.mode-over.side-right .content {
            padding-right: var(--token-width-sidebar-collapsed);
        }
    }

    &.side-right {
        flex-direction: row-reverse;
    }
}

:host([data-state='collapsed']) {
    .nav at-sidebar-trigger {
        width: 100%;
    }

    i[slot='menu-item-actions'],
    * > [data-name='sidebar-menu-item-label'] {
        opacity: 0;
    }

    at-menu {
        i[slot='menu-item-actions'],
        [data-name='menu-content-wrapper']
            [data-name='sidebar-menu-item-label'] {
            opacity: 1;
        }
    }

    .nav.collapse-offcanvas {
        width: var(--token-width-sidebar-offscreen);
        overflow: hidden;
    }

    .nav.collapse-icon {
        width: var(--token-width-sidebar-collapsed);
    }

    .nav.collapse-none {
        width: var(--token-width-sidebar);
    }
}
